.site-list {
  position: relative;
  background-color: #283645;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site-list__item {
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    z-index: 2;
    color: #ffffff;
    text-decoration: none;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
  }
}
