.contacts__wrapper {
  width: 240px;
  margin: 0 auto;
}

.contacts__title {
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 120%;
  font-weight: 300;
}

.contacts__item-title {
  font-size: 18px;
  line-height: 25px;
  font-weight: 700;
  text-align: center;
}

.contacts__item-lync {
  text-decoration: none;
  color: $primary-color;
  font-size: 24px;
  line-height: 32px;
  font-weight: 300;
  display: flex;
  justify-content: center;
  text-align: center;
}
